* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

body {
  font-family: $open_sans;
  line-height: $base-line-height;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

p {
  font-family: $open_sans;
}

h1, h2 {
  font-family: $condensed;
  font-weight: bold;
  letter-spacing: 0.9px;
}

